import { tooltip } from "../Bindings/Chakra.fs.js";

export function Tooltip(input) {
    return tooltip()((x) => {
        Object.assign(x, input.Props);
        x.paddingTop = "3px";
        x.backgroundColor = "gray.77";
        x.color = "black";
        x.zIndex = 20000;
    })(input.Props.children);
}

